.profileContainer {
    display: flex;
    height: 100vh;
    width: 100vw;
    /* position: fixed; */
    top: 0%;
    left: 0%;
    max-width: 100%;
    background-color: white;
    margin-top: 50px;
  }
  
  .profileContainer > div {
    display: flex;
    height: 100vh;
    width: 100vw;
    max-width: 100%;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  .profileContainer > div:first-child > h1 {
    color: rgba(0, 0, 0, 0.555);
    font: 500 3vmax 'Phudu', sans-serif;
    transform: translateX(-10vmax) translateY(-2vmax);
  }
  
  .profileContainer > div:first-child > img {
    width: 20vmax;
    border-radius: 100%;
    transition: all 0.5s;
    transform: translateX(8vmax) translateY(-8vmax);
  }
  
  .profileContainer > div:first-child > img:hover {
    /* transform: scale(1.05); */
  }
  
  .profileContainer > div:first-child > a {
    border: none;
    background-color: tomato;
    font: 400 1vmax "Roboto";
    color: white;
    text-decoration: none;
    padding: 0.5vmax;
    width: 30%;
    margin: 4vmax;
    text-align: center;
    transition: all 0.5s;
    /* transform: translateX(-10vmax) translateY(-2vmax); */
  }
  
  .profileContainer > div:first-child > a:hover {
    background-color: rgb(204, 78, 56);
  }
  
  .profileContainer > div:last-child {
    justify-content: space-evenly;
    align-items: flex-start;
    padding: 5vmax;
    box-sizing: border-box;
  }
  
  .profileContainer > div:last-child > div > h4 {
    color: black;
    font: 400 2vmax 'Acme', sans-serif;
  }
  
  .profileContainer > div:last-child > div > p {
    color: rgba(0, 0, 0, 0.418);
    font: 400 1.8vmax 'Grenze Gotisch', sans-serif;
    margin: 0.2vmax;
  }
  
  .profileContainer > div:last-child > div:last-child {
    display: flex;
    flex-direction: column;
    width: 60%;
  }
  
  .profileContainer > div:last-child > div:last-child > a {
    border: none;
    background-color: rgb(68, 68, 68);
    font: 400 1vmax "Roboto";
    color: white;
    text-decoration: none;
    padding: 0.5vmax;
    text-align: center;
    transition: all 0.5s;
    margin: 1vmax 0;
  }
  
  .profileContainer > div:last-child > div:last-child > a:hover {
    background-color: rgb(31, 31, 31);
  }

  .changePassword{
    margin: 20px;
  }
  .changePassword>a{
    outline: auto black;
    color: white;
    font: 400 1vmax 'Russo One', sans-serif;
    background-color: tomato;
    
    padding: 1vmax;
  }

  .Logout{
    outline: auto black;
    color: white;
    font: 400 1vmax 'Russo One', sans-serif;
    background-color: tomato;
    padding: 1vmax;
  }
  @media screen and (max-width: 600px) {
    .profileContainer {
      /* position: absolute; */
      display: flex;
      flex-direction: column;
    }
  
    .profileContainer > div:first-child > h1 {
      padding:0;
      padding-top: 80px;
      font: 500 3.2vmax 'Phudu', sans-serif;
      transform: translateY(-2vmax);
    }

    .profileContainer > div:first-child > img {
      /* width: 20vmax;
      border-radius: 100%;
      transition: all 0.5s; */
      transform: translateX(0) translateY(0);
    }
  
    .profileContainer > div:first-child > a {
      font: 400 1.7vmax "Roboto";
      padding: 1vmax;
    }
  
    .profileContainer > div:last-child {
      text-align: center;
      align-items: center;
    }
  
    .profileContainer > div:last-child > div > h4 {
      font: 400 2.5vmax 'Acme', sans-serif;
      /* padding:0; */
      margin:0;
    }
  
    .profileContainer > div:last-child > div > p {
      font: 400 2.5vmax 'Grenze Gotisch', sans-serif;
      margin: 0vmax 0;
      margin-bottom: 2.5vmax;
    }
  
    .profileContainer > div:last-child > div:last-child > a {
      font: 400 1.8vmax "Roboto";
      padding: 1vmax;
      margin: 2vmax 0;
      
    }

    .changePassword{
      margin: 20px;
      width: 55vmax;
      /* height: 55vmax */
    }

    .changePassword>a{
      outline: auto black;
      color: white;
      background-color: tomato;
      padding: 1vmax;
      font: 400 2vmax 'Russo One', sans-serif;
      
    }
    .mobile{
      margin-bottom: 100px;
    }
    .Logout{
      outline: auto black;
      color: white;
      background-color: tomato;
      padding: 1vmax;
      font: 400 2vmax 'Russo One', sans-serif;
    }
    

  }