.search{
    position:absolute;
    top: 35px;
    left: 30%;
    width: 100vw;
    z-index: 250;
}

.search>h2{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 558px;
    padding: 10px;
    background-color: white;
    position: absolute;
    left:0;
    top: 380px;

}

.search>input{
    width:558px;
    padding: 20px;
    position: absolute;
    left:0;
    height: 30px;
    top:430px;
    border: none;
}
.search>input:focus{
    outline-width: 0;
}

.search>button{
    position: absolute;
    left:0;
    top:470px;
    text-transform: inherit !important;
    background-color: #ff7779;
    color:white;
    width:558px;
    font-weight: 600;

}

.search> button:hover{
    background-color: white;
    color: #ff7779;
}
